<template>
    <div>
        <loader :isVisible="isLoading"></loader>
        <section class="main-content">
            <tg-list :IsShowAction="true" ModuleName="CustomField" SubModuleCode="CustomField" :LegendArray="[]" :showCheckBox="true"
                IdentityColumn="CUSTOM_LAYOUT_ID" :listType="['List']" :HeaderText="$t('Custom_Layout')" :ListData="LayoutListingData"
                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :SearchFields="leftSearchFields"
                :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :TotalRecords="TotalRecords"
                :SortExp="SortExp" :SortBy="SortBy" v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata"
                v-on:ActionButtonClick="actionButtonClick" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:LeftsearchButtonClick="leftCommonSearch">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'MODIFY_DATE'">
                        {{ $filters.formatDate(data.row.MODIFY_DATE) }}
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'IS_SYSTEM_DEFAULT_TEMPLATE'">
                        <label v-bind:class="{ 'switch': data.row.IS_SYSTEM_DEFAULT_TEMPLATE == false , 'switch disabled': (data.row.IS_SYSTEM_DEFAULT_TEMPLATE == true) }">
                            <input  @change="ChangeTemplateStatus(data.row, $event,'Default')"
                                id="isTrueCheckDefault" type="checkbox" v-bind:checked="data.row.IS_SYSTEM_DEFAULT_TEMPLATE"
                                class="form-check-input rdoticketfor custom-control-input dynamic">
                            <span class="slider round">
                                <span class="slider-yes" >Yes</span>
                                <span class="slider-no"  >No</span>
                            </span>                    
                        </label> 
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'IS_ACTIVE_TEMPLATE'">
                        <label v-bind:class="{ 'switch':true , 'switch disabled': (data.row.IS_SYSTEM_DEFAULT_TEMPLATE == true) }">
                            <input  @change="ChangeTemplateStatus(data.row, $event,'Active')"
                                id="isTrueCheck" type="checkbox" v-bind:checked="data.row.IS_ACTIVE_TEMPLATE"
                                class="form-check-input rdoticketfor custom-control-input dynamic">
                            <span class="slider round">
                                <span class="slider-yes" >Active</span>
                                <span class="slider-no"  >In-Active</span>
                            </span>                    
                        </label>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'MODIFY_DATE'">
                        {{ $filters.formatDate(data.row.LAST_MODIFY_DATE) }}
                    </template>
                </template>
            </tg-list>
        </section>
        <Addlayoutpopup v-if="showLayoutPopup" @closepopup='closepopup'  @refreshlist='refreshlist' :CustomlayoutId='CustomlayoutId'></Addlayoutpopup>
        <SharedRoles v-if="sharetopopup" @closepopup='closepopup' @refreshlist='refreshlist' :CustomlayoutId='CustomlayoutId'
        :MODULEID='MODULEID' :SUBMODULEID="SUBMODULEID" :LAYOUTNAME="LAYOUTNAME" :PLATFORM="PLATFORM" :LAYOUTTYPE="LAYOUTTYPE"
        :DESCRIPTION="DESCRIPTION" :LAYOUTSHARETOID="LAYOUTSHARETOID"></SharedRoles>
    </div>
</template>


<style scoped>
.is-visible {
    display: inline-block;
    height: auto;
}
</style>
<script>
import Schema from '../../src/views/Customlayoutsechema'
import Addlayoutpopup from "../components/Common/Modal/AddLayoutpopup.vue"
import SharedRoles from "../components/Common/Modal/SharedRolesPopup.vue"
import DataService from "../services/DataService";
export default {
    components: {
        Addlayoutpopup,
        SharedRoles
    },
    data() {
        return {
            isLoading: false,
            showLayoutPopup: false,
            sharetopopup: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            id: 80,
            LayoutListingData: [],
            sub_module_id: null,
            module_id: '',
            noRecord: false,
            showSharePopup: false,
            moduleNameList: [],
            subModuleNameList: [],
            moduleId: null,
            userTypeId: 1,
            reqFrom: null,
            deviceType: [{ "id": "Web", "name": "Web" }, { "id": "Andriod", "name": "Andriod" }, { "id": "Window", "name": "Window" }],
            LayoutType: [{ "id": "Add", "name": "Add" }, { "id": "View", "name": "View" }, { "id": "Edit", "name": "Edit" }],
            ddlModuleId: '',
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            searchCondition: '',
            TotalRecords: 0,
            CustomlayoutId: null,
            headertext:'',
            MODULEID:'',
            SUBMODULEID: '',
            LAYOUTNAME:'',
            PLATFORM:'',
            LAYOUTTYPE:'',
            DESCRIPTION:'',
            LAYOUTSHARETOID:''
        }
    },
    // updated: function () {
    //     this.BindActionButtonEvent();
    // },
    created: function () {
        var vm = this;
        vm.FetchData();
        vm.FetchModuleData();
        Schema.bindleftCommonSearchdropdown(vm, 'CustomField', 'Module_Name')
        Schema.bindleftCommonSearchdropdown(vm, 'CustomField', 'DeviceType')
        Schema.bindleftCommonSearchdropdown(vm, 'CustomField', 'LayOutType')

    },
    methods:
    {
        OpenAddPAge(lbl){
            var vm=this;
            var params=`ModuleName=CRM&SubModuleName=CRM_DEALS&Platform=WEB&LayoutType=Add`
            DataService.GetAssignedLayoutIdToUser(params).then(function (response) {
                 vm.CustomlayoutId=response.data;
                 vm.$router.push({ name: 'ManageCustomLayout', params: {CustomlayoutId: vm.CustomlayoutId, PreviewMode:lbl}})
            })
        },
        closepopup: function () {
            this.showLayoutPopup = false;
            this.sharetopopup = false
        },
        refreshlist: function () {
            var vm = this;
            vm.FetchData();
        },

        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                ;
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Preview':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Share':
                        action.isVisible = true;
                        break;
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {

            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'OpenAddPAge':
                    vm.OpenAddPAge('Preview');
                    break;
                case 'EditCustomLayout':
                    vm.EditCustomLayout(item);
                    break;
                case 'ShareTORole':
                    vm.ShareTORole(item);
                    break;
                case 'CloneCustomLayout':
                    vm.AddCustomLayout(item);
                    break;
                case 'DeleteSingleRecord':
                    vm.commonDeleteData(item.CUSTOM_LAYOUT_ID);
                    break;
            }
        },
        ShareTORole: function (ths) {
            var vm = this;
            if (ths.CUSTOM_LAYOUT_ID != null) {
                vm.CustomlayoutId = ths.CUSTOM_LAYOUT_ID;
                vm.MODULEID= ths.MODULE_ID;
                vm.SUBMODULEID = ths.SUB_MODULE_ID;
                vm.LAYOUTNAME = ths.LAYOUT_NAME;
                vm.PLATFORM = ths.PLATFORM;
                vm.LAYOUTTYPE = ths.LAYOUT_TYPE;
                vm.DESCRIPTION = ths.DESCRIPTION;
                vm.LAYOUTSHARETOID = ths.LAYOUT_SHARE_TO_ID;
                


                // vm.$emit('FetchSharetoRole')
                vm.sharetopopup = true;
            }

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddCustomLayout":
                    this.AddCustomLayout(event);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        EditCustomLayout: function (id) {
            
            var vm = this;
            if (id.CUSTOM_LAYOUT_ID != null) {
                vm.CustomlayoutId = id.CUSTOM_LAYOUT_ID;
            }
            vm.$router.push({
                path: "/CustomLayout/Manage/"+ vm.CustomlayoutId
            });
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading=true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "",
                        subModuleCode: "",
                        refCode: "TALYGEN_CUSTOM_LAYOUT"
                    };

                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        var box = "";
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            vm.FetchData();
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "Success", true, vm.$t("Alert"));
                        }
                    },);
                });
            }
            else {
                ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading=false;
        },
        AddCustomLayout: function (ths) {
            var vm = this;
            if (ths.CUSTOM_LAYOUT_ID != null) {
                vm.CustomlayoutId = ths.CUSTOM_LAYOUT_ID;
                vm.showLayoutPopup = true;
            }
            else {
                vm.showLayoutPopup = true;
                vm.CustomlayoutId = null;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },


        FetchModuleData: function () {
            var vm = this;
            var params = `moduleId=&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.moduleNameList = response.data.Data;
                }
                // setTimeout(function () {
                //     vm.CheckBoxBootstrap()
                // }, 500);
            })
        },

        SelectSubmodule(ths) {
            var vm = this;
            vm.ddlModuleId = ths;
            var params = `moduleId=${vm.ddlModuleId}&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.subModuleNameList = response.data.Data;
                }
            })
        },
        CloseAddPopup: function () {
            var vm = this;
            vm.showLayoutPopup = false;
        },
        clickEventOfColumn: function (row, column) {
            if (column.COLUMN_NAME == 'LAYOUT_NAME') {
                this.$router.push({
                name: 'ManageCustomLayout',
                params: { CustomlayoutId: row.CUSTOM_LAYOUT_ID }
            });
            }
        },
        clickEventOfColumn1: function (row, column) {
            if (column.COLUMN_NAME == 'LAYOUT_SHARE_TO') {
                // this.$router.push({ path: '/CRM/ViewLeadDetails/' + row.lead_id + '/CRM/CRM_LEADS' });
            }
        },
        clickEventOfColumn2: function (row, column) {
            if (column.COLUMN_NAME == 'SYSTEM_GENRATED_COLUMN') {
                // this.$router.push({ path: '/CRM/ViewLeadDetails/' + row.lead_id + '/CRM/CRM_LEADS' });
            }
        },
        columnDataFormatter: function (row, column) {
           if (column.COLUMN_NAME == 'LAYOUT_SHARE_TO') {
                // return (row.LAYOUT_SHARE_TO.split(",")).length;
                return (row.LAYOUT_SHARE_TO == '' ? '-' : row.LAYOUT_SHARE_TO.split(",").length);
            }

            else if (column.COLUMN_NAME == 'SYSTEM_GENRATED_COLUMN') {
                return (row.SYSTEM_GENRATED_COLUMN == '' ? '-' : row.SYSTEM_GENRATED_COLUMN.split(",").length);
            }
            else if (column.COLUMN_NAME == 'USER_GENRATED_COLUMN') {
                return (row.USER_GENRATED_COLUMN == '' ? '-' : row.USER_GENRATED_COLUMN.split(",").length)
            }
            else if (column.COLUMN_NAME == 'TOTAL_RECORDS') {
                return (row.USER_GENRATED_COLUMN == '' ? '' : row.USER_GENRATED_COLUMN.split(",").length) + (row.SYSTEM_GENRATED_COLUMN == '' ? '' : row.SYSTEM_GENRATED_COLUMN.split(",").length)
            }
            else {
                return row[column.COLUMN_NAME]
            }
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            const params = this.getRequestParams(this.sortBy, this.sortExp, this.pageSize, this.pageNumber, this.isPaged, this.searchcondition);
            params.pageSize = vm.PageSize;
            params.pageNumber = vm.PageNumber;
            params.searchCondition = vm.searchCondition;
            if (vm.PageNumber) { params.pageNumber = vm.PageNumber; }
            else { vm.PageNumber = params.pageNumber = 1; }
            if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
            else { vm.isPaged = params.isPaged = 1; }
            params.isPaged = vm.isPaged;
            params.sortBy = vm.SortBy;
            params.sortExp = vm.SortExp;
            var srchCondition = "";
            if (!vm.isBlank(vm.searchCondition)) { srchCondition = srchCondition + vm.searchCondition; }
            if (!vm.isBlank(vm.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vm.isBlank(vm.searchCondition)) ? (" AND " + vm.conditionForInstantSearch) : vm.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition;
            }
            DataService.GetCustomLayout(params).then(function (response) {
                vm.isLoading = false;
                if (JSON.parse(response.data) != null) {
                    vm.LayoutListingData = JSON.parse(response.data).data;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'LAYOUT_NAME') {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn,
                                // formatter: vm.columnDataFormatter
                            }
                        }
                        if (column.COLUMN_NAME == 'IS_SYSTEM_DEFAULT_TEMPLATE') {
                            column.settings = {
                                isInSlot: true,
                                clickEvent: vm.ChangeTemplateStatus
                            }
                        }
                        if (column.COLUMN_NAME == 'IS_ACTIVE_TEMPLATE') {
                            column.settings = {
                                isInSlot: true,
                                clickEvent: vm.ChangeTemplateStatus
                            }
                        }
                        if (column.COLUMN_NAME == 'LAYOUT_SHARE_TO') {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn1,
                                formatter: vm.columnDataFormatter

                            }
                        }
                        if (column.COLUMN_NAME == 'LAST_MODIFY_DATE') {
                            column.settings = {
                                inSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'SYSTEM_GENRATED_COLUMN') {
                            column.settings = {
                                // inSlot:true
                                clickEvent: vm.clickEventOfColumn2,
                                formatter: vm.columnDataFormatter
                            }
                        }
                        if (column.COLUMN_NAME == 'USER_GENRATED_COLUMN') {
                            column.settings = {
                                // inSlot:true
                                // clickEvent: vm.clickEventOfColumn2,
                                formatter: vm.columnDataFormatter
                            }
                        }
                        if (column.COLUMN_NAME == 'TOTAL_RECORDS') {
                            column.settings = {
                                // inSlot:true
                                // clickEvent: vm.clickEventOfColumn2,
                                formatter: vm.columnDataFormatter
                            }
                        }
                    })
                    if (vm.LayoutListingData != null) {
                        vm.TotalRecords = vm.LayoutListingData[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        
                    }
                    else {
                        vm.noRecord = true;
                        vm.LayoutListingData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }

                }
                else {
                    vm.noRecord = true;
                    vm.LayoutListingData = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }

                //setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                //setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);

            })
            vm.isLoading = false;
        },
        SharePopup() {
            var vm = this;
            vm.showSharePopup = true;
        },
        ClosePopupShare() {
            var vm = this;
            vm.showSharePopup = false;

        },
        ChangeTemplateStatus: function (itm, evt, fldstatus) {
            var vm = this;
            //var isdefault = $("#isTrueCheck").is(":checked"); 
            var  isdefault=!itm.IS_ACTIVE_TEMPLATE;
            var is_default_template=!itm.IS_SYSTEM_DEFAULT_TEMPLATE;
            var update_field=fldstatus;
            if(isdefault)
                isdefault=1;
            else 
                isdefault=0;
            if(is_default_template) is_default_template=1; else is_default_template=0;  
                var params = `custom_layout_id=${itm.CUSTOM_LAYOUT_ID}&is_active_template=${isdefault}&is_default_template=${is_default_template}&update_field=${update_field}`;
                DataService.ChangeTemplateStatus(params).then(function (response) {
                    if (response.data == "1") {
                       // itm.IS_ACTIVE_TEMPLATE= !itm.IS_ACTIVE_TEMPLATE
                        vm.ShowAlert(vm.$t('CustomLayoutStatusSuccess', [vm.$t('Custom_Layout')]), "success", true, vm.$t("Alert"));                      
                        vm.FetchData();  
                    }
                    if (response.data == "2") {
                       
                        // itm.IS_ACTIVE_TEMPLATE= !itm.IS_ACTIVE_TEMPLATE
                        vm.ShowAlert(vm.$t('CustomLayoutStatusSuccessDefault', [vm.$t('Custom_Layout')]), "success", true, vm.$t("Alert"));
                        vm.FetchData();
                        
                    }
                    if (response.data == "") {
                       // itm.IS_ACTIVE_TEMPLATE= !itm.IS_ACTIVE_TEMPLATE
                        vm.ShowAlert(vm.$t('CustomLayoutStatusDefault', [vm.$t('Custom_Layout')]), "success", true, vm.$t("Alert"));
                        
                    }
                })
                $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                $("#DeleteMultiple").addClass("disabled");
        }

    }

}
</script>