function bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    let params = {};
    params["moduleName"] = modulename; 
    params["type"] = filtername 
    var DeviceType=[{ "id": "Web", "name": "Web" }, { "id": "Mobile", "name": "Mobile" }];     
    var LayOutType=[{ "id": "Add", "name": "Add" }, { "id": "View", "name": "View" }, { "id": "Edit", "name": "Edit" }]
    DataService.Modulenamedata(params)
        .then(function (response) {
            var json = response.data.Data;                
            if (json != null) {  
                if(filtername == "Module_Name"){
                    vm.leftSearchFields[1].listOptions =json.map((item) => {
                        const container = {};                    
                        container.name = item.module_name;
                        container.value = (item.module_id).toString();                    
                        return container;
                    });     
                }                                 
            }
        });
        if(filtername == "DeviceType"){
            vm.leftSearchFields[2].listOptions =DeviceType.map((item) => {
                const container = {};                    
                container.name = item.name;
                container.value = (item.id).toString();                    
                return container;
            });     
        } 
        if(filtername == "LayOutType"){
            vm.leftSearchFields[3].listOptions =LayOutType.map((item) => {
                const container = {};                    
                container.name = item.name;
                container.value = (item.id).toString();                    
                return container;
            });     
        } 
 };
export default { buttons, leftsearchSchema, listActions,TableHeader, bindleftCommonSearchdropdown };


