<script setup>
import DataService from "@/services/DataService";

</script>
<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <button type="button" class="close" @click=closepopup()>
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 name="header" class="modal-title">
                            {{ $t('AddLayout') }}
                        </h4>
                    </div>
                    <Form>

                        <div class="modal-body"> 
                        <div class="row">
                            
                            <div class="col-lg-12">
                                <div class="form-group">
                                    <Field name="SharetoRole"  label="Share To Role"
                                         v-model="SharetoRole"  >
                                        <label>{{ $t('SharetoRole') }}:</label>
                                        <Multiselect v-model="SharetoRole" :options="Options" mode="tags"   @Select="onChangeOnSelectDropdown($event)" 
                                            v-bind="field" :close-on-select="false" :searchable="true" :taggable="false" label="name" :multiple="true" 
                                            placeholder="Select">
                                        </Multiselect>   
                                    </Field>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-12 mt-2">
                                <div class="float-right">
                                    <a class="btn btn-success mr-2 my-sm-0 " href="javascript:;"
                                        v-on:click="ModifySharetoRole()" :title="$t('Submit')">
                                        <em class="fa fa-floppy-o pl-2 pr-2"></em>
                                        {{ $t("Submit") }}
                                    </a>
                                    <button class="btn btn-primary delete-btn" v-on:click="closepopup"
                                        :title="$t('Cancel')">
                                        <em class="fa fa-times pl-2 pr-2"></em>
                                        {{ $t("Cancel") }}
                                    </button>
                                </div>
                        </div>
                        </div>

                        </div>
                     

                    </Form>
                </div>

                <div class="my-popups" v-if="AssignRolePopup">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <h4 class="modal-title py-3 ">
                                {{ $t('AssignRole') }}
                            </h4>
                        </div>
                        <div class="modal-body">
                            <div class="table-responsive table-fix-header">
                                <table id="example" class="table table-bordered mt-0" style="width:100%" aria-label="RolesCheckData listing">
                                    <thead class="thead-bg">
                                        <tr class="text-center">
                                            <th class="text-center">
                                                <span>Layout Name</span>
                                            </th>
                                            <th class="text-center">
                                                <span>PlatForm</span>
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="item in RolesCheckData" :key="item" >
                                            <td class="text-center">
                                                <span class="text-center">
                                                    {{ item.layoutname }}
                                                </span>
                                            </td>
                                            <td class="text-center">
                                                <span class="text-center">
                                                    {{ item.platform }}
                                                </span>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div class="row">
                                <div class="col-12 mt-2">
                                    <div class="float-right">
                                        <a class="btn btn-success mr-2 my-sm-0 " href="javascript:;" :title="$t('Submit')" v-on:click="closeAssignpopup($event)">
                                            <em class="fa fa-floppy-o pl-2 pr-2"></em>
                                            {{ $t("un assgin from previous layout and assign to new") }}
                                        </a>
                                        <button class="btn btn-primary delete-btn my-sm-0" :title="$t('Cancel')" v-on:click="roleListingDelete($event)">
                                            <em class="fa fa-times pl-2 pr-2" ></em>
                                            {{ $t("Cancel") }}
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>

        </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        showlayoutpopup: {
            type: Boolean
        },
        CustomlayoutId: {
            type: Number,
            required: true
        },
        MODULEID: {
            type: Number,
            required: true
        },
         SUBMODULEID:{
            type: Number,
            required: true
         }, 
         LAYOUTNAME: {
            type: String,
            required: true
         }, 
         PLATFORM: {
            type: String,
            required: true
         }, 
         LAYOUTTYPE: {
            type: String,
            required: true
         }, 
         DESCRIPTION :{
            type: String,
            required: true
         }, 
         LAYOUTSHARETOID :{
            type: String,
            required: true
         }
    },
    data() {
        return {

            SharetoRole: [],
            shearedtypeRole: [],
            searchable: true,
            reqFrom: null,
            loggeduserid: '',
            companyId: '',
            pageSize: 10000,
            pageNum: 1,
            role_id: '',
            Options: [],
            Cloneheader: false,
            AssignRolePopup:false,
            RolesCheckData:[],
            RolesCheckReturnData:[],
            ShareToRoleitem:'',
        }
    },
    created() {
        var vm = this;
        vm.loggeduserid = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.FetchRoleData();
        vm.FetchClone();
    },
    methods: {
        closeAssignpopup() {
            debugger;
            var vm = this;
            vm.AssignRolePopup = false;           
        },
        roleListingDelete: function (e) {
            var vm = this;
            vm.AssignRolePopup = false;
            vm.SharetoRole.pop();
   
        },
        onChangeOnDropdownChange: function (e, field) {
            var vm = this;
            var lastItem = vm.SharetoRole.length-1;
            vm.ShareToRoleitem=  vm.SharetoRole[lastItem];
            var params = `module_id=${vm.MODULEID}&sub_module_id=${vm.SUBMODULEID}&layout_name=${vm.LAYOUTNAME}&platform=${vm.PLATFORM}&layout_type=${vm.CustomlayoutId}&description=${vm.DESCRIPTION}&rolesdetail=${ vm.ShareToRoleitem}`;
            DataService.CheckRolesOnCustomLayout(params).then(function (response) {
                vm.RolesCheckReturnData = response.data.data;
                if (response.data.data != '' && response.data.data != null) {
                    vm.RolesCheckData = [];
                    vm.RolesCheckReturnData[0].forEach((item, index) => {
                        vm.RolesCheckData.push({
                            Customlayout_id: item.custom_layout_id,
                            layoutname: item.layout_name,
                            platform: item.platform
                        });
                    })
                    vm.AssignRolePopup = true;
                }
            })
        },
        FetchRoleData: async function (e, field) {
            var vm = this;
            vm.Options= [];
            var params = `companyId=${vm.companyId}&pageSize=${vm.pageSize}&statusId=1001`;
            await DataService.Roledata(params).then(function (response) {
                if (response.data.length != null) {
                    vm.shearedtypeRole = response.data;
                    vm.shearedtypeRole.forEach((item, index) => {
                        vm.Options.push({
                            name: item.rolE_NAME,
                            value: item.rolE_ID,
                        });
                    });
                }
            });
            if (vm.CustomlayoutId != null) {
                        vm.FetchClone();
                    }
            vm.isLoading = false;
        },
        FetchClone: function () {
            var vm = this;
            vm.Cloneheader = true;
            var params = `custom_layout_id=${vm.CustomlayoutId}`;
            DataService.GetCustomLayoutPopUp(params).then(function (response) {
                var CloneDatA = JSON.parse(response.data);
                if (CloneDatA != null) {
                    var ShareRole = CloneDatA.shareto;
                    var objRoleData = [];
                            ShareRole.map(item => {
                                debugger
                                var checkvalue = vm.shearedtypeRole.find(X => X.rolE_ID == item.role_id)
                                objRoleData.push({
                                    name: checkvalue.rolE_NAME,
                                    value: checkvalue.rolE_ID
                                })                     
                            });
                    vm.SharetoRole= objRoleData ;
                }
            })
        },
        closepopup(item) {
            var vm = this;
            vm.$emit('closepopup', item);
            vm.$emit('refreshlist',item);
        },
        ModifySharetoRole: function () {
            var vm = this;
            var commasepratedRoleId = [...new Set(vm.SharetoRole)];
            var params = `custom_layout_id=${vm.CustomlayoutId}&rolesdetail=${commasepratedRoleId}`;
            DataService.ModifyShareWithOnCustomLayout(params).then(function (response) {
                if (response.statusText == "OK") {
                    vm.closepopup();   
                }
            }).catch(err => {
                console.log(err)
            })
          
        },
    }
}
</script>